import React from 'react'

const Home = () => (
  <div className="intro">
    <p>Dear family and friends</p>

    <p>Welcome to our wedding website! We are very pleased to invite you to our wedding in Phuket this November. As many of you will be travelling long distances to attend our wedding, we thought we would provide some information here which we hope you will find useful when making travel arrangements and planning your stay in Phuket. If you wont be able to come to Phuket for our wedding, please dont worry about it - you can share with us some of our experiences of the wedding here at this website. </p>

    <p>Please feel free to post any questions you have on our questions board, and please do come back and visit from time to time as we will be posting updates!</p>

    <p>Best wishes<br />
       Dianna &amp; Jon</p>
  </div>
)

export default Home



